/* Larger than 800px */
@media (min-width: 600px) {
  #res {
    width: 650px;
  }
}

/* Smaller than or equal to 800px */
@media (max-width: 800px) {
  #res {
    flex-wrap: wrap;
  }

  .container { 
    flex-direction: column; 
    /* align-items: center;  */
 
  }
  .col-5 {
    width: 100%; 
    text-align: center;
    margin-bottom: 20px; /* Add some margin between columns on smaller screens */
  }

  .col-5 img {
    margin-top: 20px; /* Add some space between text and image on smaller screens */
  }
}
